/* Font Family */
$s-font--family-1: 'Brix Sans', sans-serif;
$s-font--family-2: 'Kapra', sans-serif;


/* Font Weight */
$s-font--weight-normal: 400;
$s-font--weight-bold: 700;
$s-font--heading-weight: $s-font--weight-normal;


/* Fonts */

$s-font--type-base-size: t-to-rem(16);
$s-font--type-base-size-laptop: t-to-rem(20);
$s-font--type-base-size-desktop: t-to-rem(22);
$s-font--type-base-family: $s-font--family-1;
$s-font--type-base-letter: 0;
$s-font--type-base-line: 1.3;

$s-font--type-1-size: $s-font--type-base-size;
$s-font--type-1-size-laptop: $s-font--type-base-size-laptop;
$s-font--type-1-size-desktop: $s-font--type-base-size-desktop;

// eg header footer links
$s-font--type-2-size: t-to-rem(18);
$s-font--type-2-size-laptop: t-to-rem(20);;
$s-font--type-2-size-desktop:  t-to-rem(22);
$s-font--type-2-letter: 0;
$s-font--type-2-line: 1.27;

$s-font--type-3-size: t-to-rem(32);
$s-font--type-3-size-laptop: t-to-rem(34);
$s-font--type-3-size-desktop: t-to-rem(36);;
$s-font--type-3-letter: -.01em;
$s-font--type-3-line: 1.08;

// eg copyright
$s-font--type-4-size: t-to-rem(12);
$s-font--type-4-size-laptop: t-to-rem(13);
$s-font--type-4-size-desktop: t-to-rem(14);


// eg hero copy
$s-font--type-5-size: t-to-rem(16);
$s-font--type-5-size-laptop: t-to-rem(17);
$s-font--type-5-size-desktop: t-to-rem(18);
$s-font--type-5-line: 1.35;

// eg newsletter placeholder
$s-font--type-6-size: t-to-rem(14);
$s-font--type-6-size-laptop: t-to-rem(18);
$s-font--type-6-size-desktop: t-to-rem(22);
$s-font--type-6-line: 1.27;
$s-font--type-6-letter: -.01em;

$s-font--type-7-size: t-to-rem(14);
$s-font--type-7-size-laptop: t-to-rem(16);;
$s-font--type-7-size-desktop:  t-to-rem(18);
$s-font--type-7-letter: 0;
$s-font--type-7-line: 1.27;

// eg product description accordion
$s-font--type-8-size: t-to-rem(16);
$s-font--type-8-size-laptop: t-to-rem(18);
$s-font--type-8-size-desktop:  t-to-rem(18);
$s-font--type-8-letter: 0;
$s-font--type-8-line: 1.27;


/* Heading Font */
$s-font--heading-1-size: t-to-rem(54);
$s-font--heading-1-size-laptop: t-to-rem(100);
$s-font--heading-1-size-desktop: t-to-rem(145);
$s-font--heading-1-family: $s-font--family-2;
$s-font--heading-1-letter: -.01em;
$s-font--heading-1-line: 0.92;

$s-font--heading-2-size: t-to-rem(36);
$s-font--heading-2-size-laptop: t-to-rem(64);
$s-font--heading-2-size-desktop: t-to-rem(96);
$s-font--heading-2-family: $s-font--family-2;

$s-font--heading-3-size: t-to-rem(36);
$s-font--heading-3-size-laptop: t-to-rem(66);
$s-font--heading-3-size-desktop: t-to-rem(96);
$s-font--heading-3-line: 1;
$s-font--heading-3-letter: 0em;

$s-font--heading-4-size: t-to-rem(32);
$s-font--heading-4-size-laptop: t-to-rem(52);
$s-font--heading-4-size-desktop: t-to-rem(72);
$s-font--heading-4-letter: 0;
$s-font--heading-4-line: 1.1;

$s-font--heading-5-size: t-to-rem(28);
$s-font--heading-5-size-laptop: t-to-rem(38);
$s-font--heading-5-size-desktop: t-to-rem(48);
$s-font--heading-5-letter: 0;
$s-font--heading-5-line:  1.2;


//eg quad heading
$s-font--heading-6-size: t-to-rem(24);
$s-font--heading-6-size-laptop: t-to-rem(30);
$s-font--heading-6-size-desktop: t-to-rem(36);
$s-font--heading-6-weight: $s-font--weight-bold;
$s-font--heading-6-line: 1.1;
$s-font--heading-6-letter: -0.01em;;


$s-font--heading-7-size: t-to-rem(14);
$s-font--heading-7-size-laptop: t-to-rem(14);
$s-font--heading-7-size-desktop: t-to-rem(14);
$s-font--heading-7-weight: $s-font--weight-bold;
$s-font--heading-7-line: 1.1;
//$s-font--heading-7-letter: -0.05em;
$s-font--heading-7-letter:0;

$s-font--heading-8-size: t-to-rem(32);
$s-font--heading-8-size-laptop: t-to-rem(40);
$s-font--heading-8-size-desktop: t-to-rem(48);
$s-font--heading-8-line: 1.2;







/* Titles */

//eg quad pretitle
$s-font--title-1-size: t-to-rem(13);
$s-font--title-1-size-laptop:  t-to-rem(14);
$s-font--title-1-size-desktop:  t-to-rem(15);
$s-font--title-1-line: 1.2;
$s-font--title-1-letter: .05em;

$s-font--title-2-size: t-to-rem(14);
$s-font--title-2-size-laptop:  t-to-rem(16);
$s-font--title-2-size-desktop:  t-to-rem(18);
$s-font--title-2-line: 1.2;

$s-font--title-3-size: t-to-rem(11);
$s-font--title-3-size-laptop: t-to-rem(15);
$s-font--title-3-size-desktop: t-to-rem(18);
$s-font--title-3-letter: 0.13em;
$s-font--title-3-weight: $s-font--weight-bold;


