

//=== Base font sizing & styles ===//

@mixin t-font--type-base {
  font-size: $s-font--type-base-size;
  letter-spacing: $s-font--type-base-letter;
  line-height: $s-font--type-base-line;
  font-family: $s-font--type-base-family;
  color: $s-color--font-1;


  @include t-media-query($s-medium-up) {
    font-size: $s-font--type-base-size-laptop;
  }
  @include t-media-query($s-xlarge-up) {
    font-size: $s-font--type-base-size-desktop;
  }
}

@mixin t-font--type-1 {
  @include t-font--type-base;
  font-size: $s-font--type-1-size;

  @include t-media-query($s-medium-up) {
    font-size: $s-font--type-1-size-laptop;
  }
  @include t-media-query($s-xlarge-up) {
    font-size: $s-font--type-1-size-desktop;
  }
}


