//=== Headings, titles and subtitles ===//

// Base heading styles
@mixin t-font--heading {
  margin: 0;
  line-height: 1;
  letter-spacing: 0;
  font-weight: $s-font--weight-normal;
}


@mixin t-font--heading-1 {
  @include t-font--heading;
  font-family: $s-font--heading-1-family;
  font-size: $s-font--heading-1-size;
  line-height: $s-font--heading-1-line;
  letter-spacing: $s-font--heading-1-letter;
  text-transform: uppercase;

  @include t-media-query($s-medium-up) {
    font-size: $s-font--heading-1-size-laptop;
  }

  @include t-media-query($s-xlarge-up) {
