.o-cart-upsell-drawer {
  margin-top: 2.5rem;
  border-top: 1px solid rgba($s-color--background-secondary, 0.1);
  border-bottom: 1px solid rgba($s-color--background-secondary, 0.1);
  padding: 2.125rem 1rem;
  background-color: $s-color--background-septenary;

  &__container {
    max-height: 0;
    transition: max-height $s-anim--time-med ease-in-out;

    &.is-visible {
      max-height: t-to-rem(350);
    }
  }

  &__title {
    text-align: center;

    &.o-heading--7 {
      margin-bottom: 1.5625rem;
    }
  }

  &__middle-container {
    display: flex;
    flex-direction: column;
  }

  &__bundle {
    display: flex;
    @include t-font--type-7;
    font-size: t-to-rem(16);

    &-image {
      height: 6rem;
      width: auto;
      margin-right: t-to-rem(40);
    }

    &-container {
      display: flex;
      align-items: center;
    }

    &-title {
      margin-left: t-to-rem(4);
      margin-bottom: 0.625rem;

      &-prefix {
        font-weight: 700;
      }
    }

    &-text {
      color: $s-color--font-8;
    }

    &-price {
      margin-top: 0.4375rem;
      @include t-font--type-5;

      &-compare {
        text-decoration: line-through;
        color: rgba($s-color--font-1, .5);
        margin-right: t-to-rem(8);
      }

      .o-cart-upsell-drawer__button-container & {
        display: none
      }
    }
  }

  &__upsell-icon {
    @include t-width-and-height(20);
    margin-right: t-to-rem(8);

    svg {
      circle {
        fill: $s-color--font-10;
      }
    }
  }

  &__button-container {
    margin-left: auto;
  }

  &__add-to-cart {
    @include t-width-and-height(32);
    background-color: $s-color--background-tertiary;
    border-radius: 100%;
    transition: all $s-anim--time-short $s-anim--ease-out;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin-left: t-to-rem(16);

    &:hover {
      background-color: $s-color--background-quinary;
    }

    svg {
      @include t-width-and-height(12);

      path {
        fill: $s-color--font-6;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 1.5rem 3rem 2.375rem;

    &__title {
      text-align: left;
    }

    &__bundle {
      &-container {
        margin-top: 0.5rem;
      }

      &-image {
        height: 7.5rem;
        width: auto;
        margin-right: 3.5rem;
      }

      &-title {
        @include t-font--type-base;
        margin-left: 0;
        margin-bottom: 0;

        &-prefix {
          display: block;
          display: inline-block;
        }

        br {
          display: none;
        }
      }

      &-text {
        margin-top: 0.5625rem;
      }

      &-price {
        text-align: right;

        .o-cart-upsell-drawer__button-container & {
          display: flex
        }

        .o-cart-upsell-drawer__middle-container & {
          display: none;
        }

        font-size: t-to-rem(16);
        margin-left: auto;
        flex-direction: column;

        &-compare {
          margin-bottom: t-to-rem(4);
          margin-right: unset;
        }
      }
    }

    &__upsell-icon {
      @include t-width-and-height(32);
      margin-right: 1rem;
    }

    &__add-to-cart {
      @include t-width-and-height(40);
      margin-left: t-to-rem(35);
      margin-bottom: t-to-rem(12)
    }
  }
}