@import '../base';
@import '../objects/cart-upsell-drawer';

$c-ajax-cart--transition: all $s-anim--time-short $s-anim--ease-out;
/*    SCROLLBAR LIKE MAC OS    */
.scrollbar::-webkit-scrollbar {
  background-color:#fff;
  width:16px
}

/* background of the scrollbar except button or resizer */
.scrollbar::-webkit-scrollbar-track {
  background-color:#fff
}
.scrollbar::-webkit-scrollbar-track:hover {
  background-color:#f4f4f4
}

/* scrollbar itself */
.scrollbar::-webkit-scrollbar-thumb {
  background-color:#babac0;
  border-radius:16px;
  border:5px solid #fff
}
.scrollbar::-webkit-scrollbar-thumb:hover {
  background-color:#a0a0a5;
  border:4px solid #f4f4f4
}

/* set button(top and bottom of the scrollbar) */
.scrollbar::-webkit-scrollbar-button {display:none}
/*    SCROLLBAR LIKE MAC OS    */
html,
body {
  &.is-ajax-cart-open {
    overflow: hidden;
    height: 100%;
  }
}

.c-section__ajax-cart {
  overflow: hidden;
}

.c-ajax-cart {
  position: fixed;
  display: none;
  z-index: 99;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  &__error {
    color: $s-color--red;
    opacity: 1;
    height: 0;

    &.is-hidden {
      opacity: 0;
    }
  }

  &__empty {
    padding-top: 3rem;
    text-align: center;
  }

  &__wrapper {
    height: 100%;
    position: relative;
    transition: $c-ajax-cart--transition;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  // Drawer itself
  &__inner {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    max-width: 30rem;
  }

  &__backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgba($s-color--background-secondary, 0.7);
    transition: $c-ajax-cart--transition;
    //z-index: -1;
  }

  &__drawer {
    background: white;
    height: calc(100% - 44px);
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow-y: auto;
    position: relative;
    transform: translateX(100%);
    transition: $c-ajax-cart--transition;
    z-index: 2;
  }

  &__header-close,
  &__item-remove {
    display: block;
    margin-left: auto;
    cursor: pointer;

    * {
      display: block;
      stroke: $s-color--blue-green;
    }
  }

  &__item-remove {
    height: 12px;
    width: 12px;
  }

  &__header-close {
    width: 18px;
    height: 18px;
  }

  &__header-close.is-outer {
    display: none;
  }

  // Transition states
  &.is-active {
    display: block;
  }

  &.is-visible &__backdrop {
    opacity: 1;
  }

  &.is-visible &__drawer {
    transform: translateX(0);
  }

  // Header Area (cart quantity, shipping bar, etc)
  &__header {
    &-inner {
      display: flex;
      padding: 16px 40px;
      align-items: center;
      border-bottom: 0.0625rem solid rgba($s-color--blue-green, 0.1);
      margin-bottom: 17px;
    }

    &-amount {
      border: 0.0625rem solid rgba($s-color--aqua, 0.4);
      border-radius: 50%;
      width: 1.875rem;
      height: 1.875rem;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 14px;
    }
  }

  &__shipping {
    margin-bottom: 1.4375rem;
  }

  // Line items
  &__lines {
    flex: 1;
    background: white;
  }

  &__item {
    display: flex;
    align-items: flex-start;
    align-items: stretch;
    border-bottom: 0.0625rem solid rgba($s-color--blue-green, 0.1);

    &-image {
      width: 120px;
      height: 120px;
      background: $s-color--light-blue;
      flex-shrink: 0;
      position: relative;

      &-picture {
        width: 100%;
        height: 100%;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        object-fit: contain;
        position: absolute;
      }
    }

    &-remove {
      height: fit-content;
      width: fit-content;
      padding: 10px;
      & > svg {
        width: 12px;
        height: 12px;
      }
    }

    &-body {
      padding: 15px 25px 0px 25px;
      position: relative;
      width: 100%;
    }

    &-title {
      @include t-font--type-5;
      display: block;
      width: 85%;
    }

    &-variant-title {
      margin-top: t-to-rem(8);
      font-size: 1.125rem;
      color: $s-color--blue-green;
    }

    &-subtitle {
      color: $s-color--grey-green;
      width: 85%;
    }

    &-price {
      @include t-font--type-5;
      margin-top: 14px;
      margin-bottom: 22px;
      display: block;

      &.has-compare {
        margin-left: t-to-rem(8);
      }

      &-compare {
        text-decoration: line-through;
        color: rgba($s-color--font-1, .5);
      }
    }

    &-remove {
      position: absolute;
      right: 1.275rem;
      top: 0.65rem;
    }

    &-money {
      margin-top: 18px;
      position: relative;

      .o-cart-quantity__button {
        width: 24px;
        height: 24px;
      }

      .o-cart-quantity__number {
        width: 1.875rem;
      }
    }
  }

  &__bundle {
    &-items {
      font-weight: 700;
      text-transform: uppercase;
      margin-top: 13px;
    }

    &-item {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }

  // Footer
  &__footer {
    background: $s-color--light-blue;
    border-top: 1px solid rgba($s-color--background-secondary, 0.1);
    border-bottom: 1px solid rgba($s-color--background-secondary, 0.1);

    &-subtotal {
      border-bottom: 0.0625rem solid rgba($s-color--blue-green, 0.1);
      display: flex;
      justify-content: space-between;
      padding: 1.0625rem 2.5625rem;
    }

    &-upsell {
      padding: 1.0625rem 2.5625rem;
    }

    &-buttons {
      width: 100%;
      border-top: 0.0625rem solid rgba($s-color--blue-green, 0.2);
      z-index: 2;
    }

    &-cart {
      width: 100%;
      text-align: center;
    }

    &-checkout {
      display: flex;
      width: 100%;

      .has-invalid-quantity & {
        &.o-btn {
          pointer-events: none;
          background: $s-color--disabled-grey;
        }
      }
    }

    &-upsell-title {
      text-align: center;
      margin-bottom: 1.25rem;
    }

    &-msg {
      border-bottom: 0.0625rem solid rgba($s-color--blue-green, 0.1);
      padding: 30px 24px 28px;

      &-link {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &-icon {
        width: 30px;
        height: 30px;
      }

      &-text {
        border-left: 0.125rem solid rgba($s-color--blue-green, 0.1);
        margin-left: 20px;
        padding-left: 13px;

        p {
          color: $s-color--font-8;

          &:first-of-type {
            color: $s-color--font-2;
          }
        }

        span {
          color: $s-color--font-1;
          text-decoration: underline;
        }
      }
    }
  }

  &__prod {
    display: flex;
    align-items: center;
    background: $s-color--white;
    padding: 1.375rem 1.25rem;

    &-image {
      width: 2.625rem;
      flex-shrink: 0;
    }

    &-title {
      width: 100%;
    }

    &-star {
      width: 0.875rem;
      flex-shrink: 0;
      margin-left: 0.75rem;
      margin-right: 0.3125rem;
      margin-top: 0.1875rem;

      svg {
        width: 100%;
        display: block;
        height: auto;
      }
    }

    &-money {
      display: block;
    }

    &-details {
      display: flex;
      align-items: flex-start;
      flex-grow: 1;
    }

    &-add {
      width: 2.25rem;
      height: 2.25rem;
      border-radius: 50%;
      background: $s-color--aqua;
      border-radius: 50%;
      cursor: pointer;
      transition: all 0.2s $s-anim--ease-out;
      display: flex;
      align-items: center;
      justify-content: center;
      line-height: 0;
      flex-shrink: 0;
      margin-left: 1rem;

      svg,
      svg * {
        width: 0.75rem;
        height: 0.75rem;
        fill: $s-color--white;
        display: block;
      }

      &:hover {
        background: $s-color--light-aqua;
      }
    }
  }

  @include t-media-query($s-medium-down) {

    &__item-variant {
      display: none;
    }
  }

  @include t-media-query($s-medium-up) {
    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 25px 49px 25px;
      border-bottom: 0.0625rem solid rgba($s-color--blue-green, 0.1);

      &-inner {
        border: none;
        padding: 0;
        margin-bottom: 0;
      }

      &-close.is-inner {
        display: none;
      }

      &-close.is-outer {
        display: block;
        margin: 0;
      }
    }

    &__shipping {
      margin: 0;

      &.o-shipping-progress {
        padding: 0;
      }

      .o-shipping-progress__bar {
        margin-top: 10px;
      }
    }
    &__inner {
      max-width: 50rem;
    }
    &__drawer {
      height: calc(100% - 74px);
    }

    &__bundle-items {
      margin-top: 0;
      margin-bottom: 22px;
    }

    &__item {

      &-image {
        width: 160px;
        height: 160px;
      }

      &-body {
        padding: 13px 49px;
      }

      &-title {
        @include t-font--type-base;
        margin-bottom: 1px;
      }

      &-subtitle {
        font-size: 1rem;
        margin-bottom: 14px;
      }

      &-money {
        display: flex;
        align-items: center;
        margin-top: 0;

        .o-cart-quantity__button {
          width: 32px;
          height: 32px;
        }

        .o-cart-quantity__number {
          width: 55px;
          @include t-font--type-7;
        }
      }

      &-price {
        @include t-font--type-base;
        order: 2;
        padding: 0;
        text-align: right;
        margin-left: auto;
        margin-bottom: unset;
        margin-top: 30px;
      }

      .o-cart-quantity {
        order: 1;
        margin-right: auto;
      }

      &-remove {
        right: 3.25rem;
        top: 2.4375rem;
        z-index: 20;
        padding: 0.25rem;

        & > svg {
          width: 100%;
          height: 100%;
        }
      }
    }

    &__header-close,
    &__item-remove {
      width: 1.5rem;
      height: 1.5rem;
      padding: 0.25rem;
    }

    &__footer {
      &-buttons {
        max-width: 50rem;
      }

      &-checkout {
        &.o-btn.is-primary {
          padding: 24px 32px;
        }
      }

      &-upsell {
        padding: 2.0625rem 2.4375rem 1.875rem;

        &-title {
          text-align: left;
        }
      }

      &-subtotal {
        padding: 1.875rem 3.25rem;
      }

      &-msg {
        padding: 30px 0;

        &-link {
          display: inline-flex;
          padding: 0 48px;
        }

        &-text {
          margin-left: 17px;
          padding-left: 13px;
        }

        &-content {
          &:first-child {
            margin-bottom: 4px;
          }
        }

        &-icon {
          width: 24px;
          height: 24px;
        }
      }
    }

    // Upsell product
    &__prod {
      &-image {
        width: 3.125rem;
      }

      &-star {
        margin: 0 .5rem 0 2.625rem;
        width: 2rem;
      }

      &-add {
        width: 3rem;
        height: 3rem;
        margin-left: 2.1875rem;
      }

      &-recommended {
        &:after {
          display: inline-block;
          margin: 0 0.3rem;
          content: '|';
        }
      }

      &-money {
        display: block;
        margin-left: auto;
      }

      &-title {
        display: flex;
        justify-content: flex-start;
      }
    }
  }
}